import * as React from 'react';
import { styled, createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';
import MuiAppBar, { AppBarProps as MuiAppBarProps } from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';

const mdTheme = createTheme();

interface AppBarProps extends MuiAppBarProps {
  open?: boolean;
}

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})<AppBarProps>(({ theme, open }) => ({
  background: '#1976d2',
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100%)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));



const Copyright = (props: any) => {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright Â© '}
      <Link color="inherit" target="_blank" href="">
        Dashboard
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}


function MainLayout(props:any) {
  return (
    <>
        <ThemeProvider theme={mdTheme}>
            <Box sx={{ display: 'flex'}}>
                <CssBaseline />
                <AppBar position="absolute">
                    <Toolbar>
                      <Typography
                          component="h1"
                          variant="h6"
                          color="inherit"
                          noWrap
                          sx={{ flexGrow: 1 }}
                      >
                          WebSite
                      </Typography>
                    </Toolbar>
                </AppBar>

                <Box
                  component="main"
                  sx={{
                    backgroundColor: '#edf4fc',
                    flexGrow: 1,
                    height: '100vh',
                    overflow: 'auto',
                  }}
                >
                  <Toolbar />
                  <Container maxWidth="lg" sx={{ mt: 2, mb: 4 }}>

                    <div style={{
                      position: "relative",
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      minHeight: "calc(100vh - 30vh)",
                      }}>

                      <Card variant="outlined"
                      sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        boxShadow: 'rgba(145, 158, 171, 0.24) 0px 0px 2px 0px, rgba(145, 158, 171, 0.24) 0px 16px 32px -4px;',
                        width: '80%',
                        maxWidth: '520px',
                        minHeight: '240px',
                      }}>
                        <Toolbar
                          sx={{
                            pl: { sm: 2 },
                            pr: { xs: 1, sm: 1 },
                          }}
                        >
                            <Typography
                              sx={{ flex: '1 1 100%', textAlign: 'center', marginTop: '24px' }}
                              variant="h5"
                              id="tableTitle"
                              component="div"
                            >
                              {props.heading ? props.heading : null}
                            </Typography>
                          </Toolbar>
                          <CardContent>
                            
                          {
                            props.children
                          }

                          </CardContent>
                      </Card>

                      </div>
                    <Copyright sx={{ pt: 4 }} />
                  </Container>
                </Box>

            </Box>
      </ThemeProvider>
    </>
  );
}

export default MainLayout;
