import Stack from '@mui/material/Stack';
import Alert from '@mui/material/Alert';
import Grid from '@mui/material/Grid';

export const FormErrorSingleComponent = (props:any) => {
  
  return (
    <div style={{marginBottom: '10px'}}>
    {props.children}
    {
      (props.error !== undefined && (props.error === true)) ? 
      <Stack sx={{ width: '100%', marginBottom: '10px' }} spacing={2}>
        <Alert severity="error">
            <ul style={{fontSize: "13px", fontWeight: 500, margin:"5px 10px", padding: "0px 15px"}}>
              <li>
                    {props.msg}
              </li>
            </ul>
        </Alert>
      </Stack>:null
    }
    </div>
  );
};


export const FormErrorComponent = (props:any) => {
  
  return (
    <>
    {props.children}
    {
      (props.errors !== undefined && (props.errors.length > 0)) ? 
      <Stack sx={{ width: '100%' }} spacing={2}>
        <Alert severity="error">
            <ul style={{fontSize: "13px", fontWeight: 500, margin:"5px 10px", padding: "0px 15px"}}>
              {
                props.errors.map((val:string, key:number)=>{
                  return <li key={key}>
                    {val}
                  </li>
                })
              }
            </ul>
        </Alert>
      </Stack>:null
    }
    </>
  );
};

export default FormErrorComponent;